/*------------ Variables -----------*/
:root {
  --main-clr: #667988;
  --comp-clr: #c2999f;
  --light-pink: #f7ebec;
  --light-green: #acbfa4;
  --dark-green: #558b6e;
  --white: #ffffff;
  --black: #272635;
  --form-width: 90%;
  --form-max-width: 600px;
  --bg-clr: #fff;
  --card-bg: var(--bg-clr); }

.hero.grid {
  display: grid;
  grid-template-areas: "text text" "img1 img2" "img3 img4";
  grid-gap: 1rem; }
  @media screen and (min-width: 993px) {
    .hero.grid {
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 200px 200px; } }
  .hero.grid img {
    object-fit: cover;
    height: 100%;
    width: 100%; }
  .hero.grid .grid-1 {
    grid-area: img1; }
    @media screen and (min-width: 993px) {
      .hero.grid .grid-1 {
        grid-column: 1 / span 1;
        grid-row: 1 / 2; } }
  .hero.grid .grid-2 {
    grid-area: img2; }
    @media screen and (min-width: 993px) {
      .hero.grid .grid-2 {
        grid-column: 1 / span 1;
        grid-row: 2 / 3; } }
  .hero.grid .grid-3 {
    grid-area: img3; }
    @media screen and (min-width: 993px) {
      .hero.grid .grid-3 {
        grid-column: 2 / span 1;
        grid-row: 1 / span 2; } }
  .hero.grid .grid-5 {
    grid-area: text;
    background-color: var(--main-clr);
    padding: 0 0.5rem; }
    @media screen and (min-width: 993px) {
      .hero.grid .grid-5 {
        grid-column: 3 / span 1;
        grid-row: 1 / 2; } }
    .hero.grid .grid-5 p {
      color: var(--white);
      font-size: larger;
      font-weight: 300; }
  .hero.grid .grid-4 {
    grid-area: img4; }
    @media screen and (min-width: 993px) {
      .hero.grid .grid-4 {
        grid-column: 3 / span 1;
        grid-row: 2 / 3; } }

.shop-by .temp-grid img {
  max-width: 32%; }
